.header {
    grid-column: 1 / -1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #f5f5f5;
    border-bottom: 1px solid #ddd;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  .search-bar {
    flex: 1;
    margin: 0 20px;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .calendar, .notification, .profile-pic {
    margin-left: 15px;
    font-size: 18px;
    cursor: pointer;
  }
  