.attendance-table {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
  }
  
  .attendance-table h3 {
    margin-bottom: 16px;
    color: #333;
    font-size: 20px;
  }
  
  .attendance-table table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .attendance-table th,
  .attendance-table td {
    text-align: left;
    padding: 12px;
    border-bottom: 1px solid #ddd;
  }
  
  .attendance-table th {
    background-color: #f3f4f6;
    color: #555;
  }
  
  .attendance-table tr:hover {
    background-color: #f1f1f1;
  }
  
  .table-user-image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    vertical-align: middle;
    margin-right: 8px;
    object-fit: cover;
  }
  
  .view-button {
    padding: 6px 12px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .view-button:hover {
    background-color: #0056b3;
  }
  
  @media (max-width: 768px) {
    .attendance-cards {
      flex-direction: column;
      align-items: center;
    }
  
    .attendance-card {
      width: 90%;
    }
  
    .attendance-table table {
      font-size: 14px;
    }
  }
  