.attendance-list {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}

.attendance-card {
  width: 200px;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  background-color: #f9f9f9;
  transition: transform 0.2s ease;
}

.attendance-card.selected {
  background-color: #003366; /* Blue for selected card */
  color: white;
}

.attendance-card:hover {
  transform: translateY(-5px);
}

.user-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-bottom: 12px;
}

.progress {
  display: flex;
  justify-content: space-between;
  margin: 12px 0;
}

.progress div {
  text-align: center;
}

.progress div h4 {
  width: 40px; /* Adjust size of the circle */
  height: 40px;
  line-height: 40px; /* Ensures text is centered vertically */
  border-radius: 50%;
  background-color: #d9e4f5; /* Light blue background */
  color: #003366; /* Dark blue text color */
  font-size: 16px;
  font-weight: bold;
  margin: 0 auto 8px; /* Center the circle and add spacing below */
}

.progress div p {
  margin: 0;
  font-size: 12px;
  color: #555; /* Optional lighter text color for labels */
}

.profile-button {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-size: 14px;
}

.profile-button:hover {
  background-color: #0056b3;
}
