.attendance-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    background-color: #fff;
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
  }
  
  .attendance-header h3 {
    margin: 0;
    font-size: 20px;
  }
  
  .picker {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .picker select {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fff;
  }
  