.sidebar {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border-right: 1px solid #ddd;
    padding: 10px 0;
    align-items: center;
  }
  
  .icon {
    font-size: 24px;
    margin: 15px 0;
    cursor: pointer;
    color: #555;
    transition: color 0.3s;
  }
  
  .icon:hover {
    color: #007bff;
  }
  